<template>
    <div class="box">
        <header>
            <div class="logo">
                <img src="./../../assets/logo.png" alt="">
                <span>超对称运营后台</span>
            </div>
            <div class="sign-out">
                <img src="./../../assets/exit.png" alt="">
                <span @click="signOut()">退出</span>
            </div>
        </header>
        <div class="content">
            <nav>
                <el-menu
                    default-active="$route.path"
                    router
                    class="el-menu-vertical-demo"
                    @open="handleOpen"
                    @select="handleSelect"
                    @close="handleClose">
                <el-submenu index="1">
                    <template slot="title">
                    <i class="el-icon-edit"></i>
                    <span>用户管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">{{ item.navItem }}</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="2">
                    <template slot="title">
                    <i class="el-icon-document"></i>
                    <span>网站内容</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item v-for="(item,i) in navList1" :key="i" :index="item.name">{{ item.navItem }}</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                 <el-submenu index="3">
                    <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span @click="Tips">系统管理</span>
                    </template>
                    <!--<el-menu-item-group>
                        <el-menu-item v-for="(item,i) in navList1" :key="i" :index="item.name">{{ item.navItem }}</el-menu-item>
                    </el-menu-item-group>-->
                </el-submenu>
                </el-menu>
            </nav>
            <div class="section">
                <router-view></router-view>
            </div>
        </div>

    </div>
</template>
<script>
import Axios from 'axios'
import { javaServiceUrl } from './../../util/constant.js'

export default {
    data(){
        return{
            token:"",
            navList:[
                {
                    name:'/roleManagement',navItem:'角色管理'
                },{
                    name:'/userMnagement',navItem:'网站用户管理'
                },{
                    name:'/apiUser',navItem:'API用户管理'
                }
            ],
            navList1:[
                {
                    name:'/fileUpload',navItem:'文件上传'
                },{
                    name:'/newsRecommendation',navItem:'首页新闻推荐'
                }
            ]
        }   
    },
    created(){
        var storage = window.localStorage;
        this.token = storage.getItem("token");
        this.authorization = storage.getItem("userId");

    },
     methods: {
         //退出
        // signOut(){
        // console.log(this.token)
        //     if(this.token){
        //         window.localStorage.getItem("token") == "";
        //         this.$router.push({
        //             name:'login',
        //         })
        //     }
        // },
        //  退出
        signOut(){
            Axios({
               url: javaServiceUrl + '/userPermission/logout',
               method: 'get',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
            })
            .then(res=>{
                window.localStorage.setItem("token","");
                window.localStorage.setItem("userId","");
                this.$router.push({
                    name:'login',
                })
            })
            .catch(err=>{
                console.log(err)
            })
        },

        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        
        },
        handleSelect(key,keyPath){
            // console.log(key, keyPath);
            
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        },
        Tips(){
            this.$message({
            message: '该功能还未开发！',
            type: 'warning'
            });
        }
    }
}
</script>
<style lang='scss' scoped>
.box{
    padding-top: 60px;
    // header
    header{
        height: 70px;
        width: 100%;
        line-height: 70px;
        background: rgb(96,152,226);
        color: #fff;
        display: flex;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
        justify-content: space-between;
        .logo{
            margin-left: 20px;
        }
        .sign-out{
            margin-right: 30px;
            cursor: pointer;
        }
        img{
            vertical-align: middle;
            margin-right: 10px;
        }
    }
    .content{
        display: flex;
        nav{
            width: 216px;
            height: 100vh;
            background: #fff;
            .el-menu{
                border-right:none;
            }
        }
        .section{
            flex: 1;
            margin: 30px 20px;
            //background: orange;
        }
    }
}

</style>



